import app from 'flarum/forum/app';
import { extend } from 'flarum/extend';
import CommentPost from 'flarum/common/components/CommentPost';

app.initializers.add('wusong8899-checkin-ep1', () => {
  extend(CommentPost.prototype, 'headerItems', function(items){
    const userRank = this.attrs.post.attribute("userRank");
    
    items.add(
      'checkin-ep1',
      <div className="checkinLevelBarOuter">
        <div className="checkinLevelBarInner"><div style="position:absolute">Lv {userRank}</div></div>
      </div>,
      100
    );
  });
});